// Core
import React from "react";
import { createGlobalStyle } from "styled-components";
import { CssUtils } from "../../constants/styles/CssUtils";
import { Theme } from "../../constants/Theme";
import { ThemePickerClassName, ThemePickerStyle } from "./styles.scss";

// Begin Types
// __________________________________________________________________________________________

type LXLT_ThemePicker = {};

type LXLT_Theme = {
  name: "default" | "lightcyan" | "goldenrod" | "honeydew";
  primary: string;
  secondary: string;
  background: string;
  foreground: string;
};

type LXLT_ThemePickerState = {
  availableThemes: LXLT_Theme[];
  activeTheme?: LXLT_Theme;
};

// Begin Component
// __________________________________________________________________________________________

/**
 * @name ThemePicker
 * @author Peter Laxalt
 *
 */
export class ThemePicker extends React.PureComponent<
  LXLT_ThemePicker,
  LXLT_ThemePickerState
> {
  constructor(props: LXLT_ThemePicker) {
    super(props);

    this.state = {
      availableThemes: [
        {
          name: "default",
          primary: Theme.Color.Primary,
          secondary: Theme.Color.Secondary,
          background: Theme.Color.Background,
          foreground: Theme.Color.Foreground,
        },
        {
          name: "lightcyan",
          primary: Theme.Color.Galaxy,
          secondary: Theme.Color.Galaxy,
          background: "lightcyan",
          foreground: Theme.Color.Galaxy,
        },
        {
          name: "goldenrod",
          primary: Theme.Color.Galaxy,
          secondary: Theme.Color.Galaxy,
          background: "goldenrod",
          foreground: Theme.Color.Galaxy,
        },
        {
          name: "honeydew",
          primary: Theme.Color.Galaxy,
          secondary: Theme.Color.Galaxy,
          background: "honeydew",
          foreground: Theme.Color.Galaxy,
        },
      ],
    };

    // this.letterCountTimer = this.letterCountTimer.bind(this);
  }

  componentDidMount() {}

  incrementLetterCount(id: number) {
    // this.setState({
    //   characters: this.state.characters.map(
    //     (character: LXLT_DharmaChar, idx: number) => {
    //       return {
    //         letter: character.letter,
    //         count: id == character.idx ? character.count + 1 : character.count,
    //         idx: idx,
    //       };
    //     }
    //   ),
    // });
  }

  render() {
    let { activeTheme, availableThemes } = this.state;

    const SetGlobalTheme = createGlobalStyle`
      ${
        activeTheme
          ? CssUtils.CreateTheme(
              activeTheme.primary,
              activeTheme.secondary,
              activeTheme.background,
              activeTheme.foreground
            )
          : ""
      }
    `;

    return (
      <>
        {activeTheme && activeTheme.name !== "default" ? (
          <SetGlobalTheme />
        ) : null}
        <ThemePickerStyle className={`${ThemePickerClassName}`}>
          {availableThemes
            ? availableThemes.map((themeItem: LXLT_Theme, idx: number) => {
                return (
                  <div
                    className={`${ThemePickerClassName}__option ${ThemePickerClassName}__option--${themeItem.name}`}
                  />
                );
              })
            : null}
        </ThemePickerStyle>
      </>
    );
  }
}
